<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="dialogMessageMs = true">添加资讯</el-button>
		</div>

		<el-table :data="tableDate">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="资讯ID" align="center"></el-table-column>
			<el-table-column prop="type_id" label="资讯类型" align="center"></el-table-column>
			<!-- <el-table-column prop="detail" label="资讯描述" align="center"></el-table-column> -->
			<el-table-column prop="cover" label="资讯缩略图" align="center">
				<template slot-scope="scope"><img :src="scope.row.cover" style="max-width:180px;max-height:80px;" /></template>
			</el-table-column>
			<el-table-column prop="type_id" label="资讯浏览量" align="center"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button type="success" size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>

		<el-dialog :visible.sync="dialogMessageMs" title="文档编辑">
			<div class="btn">
				<div style="margin: 0 30px">
					<h3 style="height: 40px; line-height: 40px;">资讯标题</h3>
					<input type="text" v-model="form.title" style="width: 100%; height: 30px; line-height: 30px;font-size: 15px;">
					<h3 style="height: 40px; line-height: 40px;">资讯分类</h3>
					<el-select v-model="title" placeholder="请选择资讯分类">
						<el-option v-for="item in typeList" :key="item.id" :label="item.title" :value="item.id">
						</el-option>
					</el-select>
					<h3 style="height: 40px; line-height: 40px;">资讯封面图</h3>
					<el-upload action="https://api.fengniaotuangou.cn/api/upload" list-type="picture-card" :before-upload="beforeAvatarUpload"
					 :on-success="handleAvatarSuccess" :on-exceed="handleExceed">
						<i slot="default" class="el-icon-plus"></i>
						<div slot="file" slot-scope="{file}">
							<img class="el-upload-list__item-thumbnail" :src="file.url" alt="">
							<span class="el-upload-list__item-actions">
								<span v-if="!disabled" class="el-upload-list__item-delete" @click="handleRemove(file)">
									<i class="el-icon-delete"></i>
								</span>
							</span>
						</div>
					</el-upload>
					<h3 style="height: 40px; line-height: 40px;">资讯内容</h3>
					<quill-editor ref="myQuillEditor" v-model="form.detail" :options="editorOption" style="height:430px;margin:0 0 60px 0;"></quill-editor>
					<el-upload class="avatar-uploader quill-img" action="https://api.fengniaotuangou.cn/api/upload" :before-upload="beforeUpload"
					 :on-success='quillImgSuccess' style="display: none">
						<el-button size="small" type="primary" id="imgInput" element-loading-text="插入中,请稍候">点击上传</el-button>
					</el-upload>
					<div class="submit">
						<el-button type="primary" @click="newMessageMs">提交</el-button>
					</div>
				</div>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import API from '@/api/message.js'
	import {
		quillEditor
	} from 'vue-quill-editor';
	import 'quill/dist/quill.core.css';
	import 'quill/dist/quill.snow.css';
	import 'quill/dist/quill.bubble.css';
	let loading;

	export default {
		name: 'messagems',
		components: {
			quillEditor
		},
		data() {
			return {
				dialogMessageMs: false,
				form: {
					type_id: '',
					title: '',
					detail: '',
					cover: ''
				},
				title: '',
				typeList: [],
				editorOption: {
					placeholder: '请输入文档内容',
					theme: 'snow',
					modules: {
						toolbar: {
							container: [
								['bold', 'italic', 'underline', 'strike'],
								['blockquote', 'code-block'],
								[{
									'direction': 'rtl'
								}],
								[{
									'size': ['small', false, 'large', 'huge']
								}],
								[{
									'header': [1, 2, 3, 4, 5, 6, false]
								}],
								[{
									'color': []
								}, {
									'background': []
								}],
								[{
									'font': []
								}],
								[{
									'align': []
								}],
								['clean'],
								['link', 'image']
							],
							handlers: {
								'image': function(value) {
									if (value) {
										document.querySelector('.quill-img input').click()
									} else {
										this.quill.format('image', false);
									}
								}
							}
						}
					}
				},

				tableDate: [],

				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getMessageType();
			this.getMessages();
		},
		methods: {
			// 获取资讯类型
			getMessageType() {
				var self = this;
				API.messageTypes(self.currentPage).then(res => {
					self.typeList = res.data
				})
			},
			// 获取资讯内容
			getMessages() {
				var self = this;
				API.messages(self.currentPage).then(res => {
					self.tableDate = res.data
					self.totalPage = res.total
				})
			},
			// 提交文档
			newMessageMs() {
				var self = this
				if (self.title) {
					for (var i = 0; i < self.typeList.length; i++) {
						self.form.type_id = self.typeList[i].id + 1
					}
				}
				API.message(self.form).then(res => {
					self.$message.success('提交成功')
					self.dialogMessageMs = false
					self.getMessages();
					self.form = {};
					self.currentPage = 1
				})
			},
			handleEdit(index, row) {
				var self = this;
				self.dialogMessageMs = true;
				self.form = {
					type_id: row.type_id,
					title: row.title,
					detail: row.detail,
					cover: row.cover
				};
			},
			handleDelete(index, row) {
			},
			// 富文本选择图片时的事件
			beforeUpload() {
				// 显示加载动画
				loading = this.$loading({
					lock: true,
					text: '图片上传中',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			// 富文本插入图片时上传图片
			quillImgSuccess(res, file) {
				let quill = this.$refs.myQuillEditor.quill
				// 关闭动画
				loading.close();
				if (res.data) {
					let length = quill.getSelection().index;
					quill.insertEmbed(length, 'image', res.data)
					quill.setSelection(length + 1)
				} else {
					this.$message.error('图片插入失败')
				}
			},
			handleRemove(file, fileList) { //移除图片
			},
			beforeAvatarUpload(file) { //文件上传之前调用做一些拦截限制
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('上传图片大小不能超过 2MB!');
				}
				return isLt2M;
			},
			handleAvatarSuccess(res, file) { //图片上传成功
				this.imageUrl = URL.createObjectURL(file.raw);
				this.$message.success('上传成功');
				this.form.cover = file.response.data;
			},
			handleExceed(files, fileList) { //图片上传超过数量限制
				this.$message.error('上传图片不能超过1张!');
			},

			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getMessages();
			},
		}
	}
</script>

<style>
</style>
