import axios from '@/plugins/axios'
import url from './url.js'


var API = {};

// 新增资讯管理
API.message = function(data) {
	return axios.post(url.Message, data)
}

// 获取资讯类型
API.messages = function(page) {
	return axios.get(url.Messages, {page: page})
}


// 新增资讯类型
API.messageType = function(data) {
	return axios.post(url.MessageType, data)
}

// 获取资讯类型
API.messageTypes = function(page) {
	return axios.get(url.MessageTypes, {page: page})
}

export default API;